import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
export default {
	data() {
		return {
			id: null,
			num: null,
			money: '',
			isfull: false,
			iscurrent: false,
		};
	},
	computed: {

	},
	onLoad: function(option) {
		this.id = option.id;
		this.num = option.num - 1;
		this.isFull(this.id, this.num);
	},
	methods: {
		changeMoney(id, num) {
			let Userid = id.slice(0, 1);
			uni.getStorage({
				key: Userid + '_key',
				success: res => {
					for (let i = 0; i < res.data.self_huzi.length; i++) {
						if (res.data.self_huzi[i].id == id) {
							res.data.self_huzi[i].huizi_arr[num].cost = this.money;
							if (res.data.self_huzi[i].isfull) {
								res.data.self_huzi[i].huizi_arr[num].today_isfull = res.data.self_huzi[i].huizi_arr[num].today_isfull ?
									true : false;
							} else {
								res.data.self_huzi[i].isfull = true;
								res.data.self_huzi[i].huizi_arr[num].today_isfull = true;
							}
							uni.setStorageSync(Userid + '_key', res.data);
							uni.navigateTo({
								//唯一ID值传入userdetail页面
								url: '/pages/user/userdetail/lists/index?id=' + id,
								animationType: 'pop-in',
								animationDuration: 200,
							});
						}
					}
				}
			});
		},
		isFull(id, num) {
			let Userid = id.slice(0, 1);
			uni.getStorage({
				key: Userid + '_key',
				success: res => {
					for (let i = 0; i < res.data.self_huzi.length; i++) {
						if (res.data.self_huzi[i].id == id) {
							if (res.data.self_huzi[i].isfull) {
								this.isfull = true;
								if (res.data.self_huzi[i].huizi_arr[num].today_isfull) {
									this.iscurrent = true;
								} else {
									this.iscurrent = false;
								}
							}

						}
					}
				}
			});
		},
		isFetch() {
			this.$refs.popup.open();
		},
		close(done) {
			done();
		},
		confirm(done, value) {
			this.isfull = true;
			done();
		}
	},
	components: {
		uniPopup,
		uniPopupDialog
	},
};
